.Landing {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: auto;
    align-content: start;
    justify-content: stretch;
    font-family: Roboto, Verdana, sans-serif;
    color: var(--ezClockerBlack);
    background-color: var(--ezClockerBackground);
}

